html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--font-family-basic);
  font-size: calc(12px + (5) * ((100vw - 320px) / (1600)));

  @media screen and (min-width: 1920px) {
    width: 1920px;
    margin: 0 auto;
  }
}
